<template>
  <swiper :options="swiperOption" :key="keyId">
    <slot></slot>
    <!--  -->
    <div class="swiper-pagination" v-if="pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
  import {swiper} from 'vue-awesome-swiper';
  export default {
    name: 'MeSlider',
    components: {
      swiper
    },
    props: {
      direction: { //  Slides 
        type: String,
        default: 'horizontal', // 
        validator(value) { // 
          return [
            'horizontal',
            'vertical'
          ].indexOf(value) > -1;
        }
      },
      interval: { // 
        type: Number,
        default: 3000, // 3
        validator(value) { //  value 0
          return value >= 0;
        }
      },
      loop: { // true loop()
        type: Boolean,
        default: true
      },
      pagination: { // true 
        type: Boolean,
        default: true
      },
      data: {
        type: Array,
        default() {
          return [];
        }
      }

    },
    data() {
      return {
        keyId: Math.random()
      };
    },
    watch: {
      data(newData) {
        if (newData.length === 0) {
          return;
        }
        this.swiperOption.loop = newData.length === 1 ? false : this.loop;
        this.keyId = Math.random();
      }

    },
    created() {
      this.init();
    },
    methods: {
      init() {
        this.swiperOption = {
          watchOverflow: true, // 1slideswiper
          direction: this.direction, // 
          autoplay: this.interval ? { //  interval 
            delay: this.interval, // 
            disableOnInteraction: false// swiperautoplaytrue
          } : false,
          slidesPerView: 1, // slidersliders
          loop: this.data.length <= 1 ? false : this.loop, // 
          pagination: { // 
            el: this.pagination ? '.swiper-pagination' : null
          }
        };
      }
    }
  };
</script>

<style lang="scss" scoped>
  .swiper-container {
    width: 100%;
    height: 100%;
  }

</style>



